import {initState} from '../initState'

//       
export const inputMessageReducer = (state = initState, action) => {
    const {type, payload} = action
    switch (type) {
        //    
        case 'SET_MESSAGE_INPUT':
            return {...state, ...payload}
        case 'MESSAGE_INPUT_CLEAR':
            return {...payload}
        default:
            return state;
    }
}